import useLocalStorage from "@rooks/use-localstorage";
import React, { ReactElement, useContext, useEffect } from "react";
import { RouteComponentProps } from "react-router-dom";

import { SetCurrentAuthLevel } from "../../actions/user";
import { UserContext } from "../../context-containers/User";
import { IRenderJobOptions } from "../../helpers";
import { NavbarRouteContainer } from "../../route-containers/Workbench/App/Navbar";
import { ViewportRouteContainer } from "../../route-containers/Workbench/App/Viewport";
import { AuthLevel } from "../../types/user";

export interface IOwnProps<C> extends RouteComponentProps {
  appTitle: string;
  roleWhitelist: string[];
  blockingTaskKind: string;
  renderJob: (t: IRenderJobOptions<C>) => ReactElement;
}

export type Props<C> = Readonly<IOwnProps<C>>;

export function App<C>(props: Props<C>) {
  // props
  const { renderJob, appTitle, roleWhitelist, blockingTaskKind } = props;

  // hooks
  const {
    actions: { Reauthenticate, SetAppTitle, SetRoleWhitelist, SetBlockingTaskKind, SetUseTestKey },
    dispatch,
    state: { authLevel, automaticallyLoadNextJob, userAuth, useTestKey },
  } = useContext(UserContext)!;

  const [keyMode, setKeyMode] = useLocalStorage("key-mode", "production");
  const [localAuthKey, setAuthKey] = useLocalStorage("auth-key", "");
  let authKey = localAuthKey;
  const [, setLoadNext] = useLocalStorage("load-next", automaticallyLoadNextJob.toString());

  // handle auth keys embedded in URIs
  if (location.hash && location.hash.length > 0) {
    const queryStart = location.hash.indexOf("?");
    if (queryStart >= 0) {
      const parsed: { [key: string]: string } = {};
      const split = location.hash.substr(queryStart + 1).split("&");
      split.forEach(p => {
        const [name, value] = p.split("=");
        parsed[name] = value;
      });
      if (parsed.token && parsed.token.length > 0 && authKey !== parsed.token) {
        const userKey = atob(parsed.token).split(":")[0];
        const isTestKey = userKey.startsWith("user-test-");
        setAuthKey(parsed.token);
        setKeyMode(isTestKey ? "testing" : "production");
        authKey = parsed.token;
      }
      setTimeout(() => {
        const newUri = window.location.href.substr(0, window.location.href.indexOf("?"));
        if (newUri) {
          window.location.href = newUri;
        }
      }, 0);
    }
  }

  useEffect(() => {
    SetAppTitle(appTitle);
    SetRoleWhitelist(roleWhitelist);
    SetBlockingTaskKind(blockingTaskKind);
    SetUseTestKey(keyMode === "testing");
  }, []);

  useEffect(() => {
    if (authKey === null || authKey.length === 0) {
      dispatch(SetCurrentAuthLevel(AuthLevel.unauthenticated));

      return;
    }

    if (authLevel !== AuthLevel.initial) {
      return;
    }

    (async () => {
      await Reauthenticate(authKey);
    })();
  }, []);

  useEffect(() => {
    if (authLevel !== AuthLevel.authenticated) {
      return;
    }

    if (userAuth === null) {
      return;
    }

    setAuthKey(userAuth);
    setKeyMode(useTestKey ? "testing" : "production");
  }, [authLevel]);

  useEffect(() => {
    setLoadNext(automaticallyLoadNextJob ? "true" : "false");
  }, [automaticallyLoadNextJob]);

  return (
    <div id="app">
      <NavbarRouteContainer appTitle={props.appTitle} />
      <div id="viewport">
        <ViewportRouteContainer appTitle={appTitle} renderJob={renderJob} />
      </div>
    </div>
  );
}
