import useLocalStorage from "@rooks/use-localstorage";
import cx from "classnames";
import React, { useContext } from "react";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import { Alert, Form, Input } from "reactstrap";

import { ToggleRootSidebar } from "../../../actions/user";
import { UserContext } from "../../../context-containers/User";
import { AuthLevel } from "../../../types/user";

export interface IOwnProps extends RouteComponentProps {
  appTitle: string;
}

export type Props = Readonly<IOwnProps>;

function UserActions(props: Props) {
  const [, setKeyMode] = useLocalStorage("key-mode", "production");
  const [, setAuthKey] = useLocalStorage("auth-key", "");
  // props
  const { location } = props;

  // hooks
  const {
    dispatch,
    state: { authLevel, useTestKey, user },
  } = useContext(UserContext)!;

  switch (authLevel) {
    case AuthLevel.initial:
      return null;
    case AuthLevel.authenticated:
      break;
    case AuthLevel.unauthenticated:
      return (
        <>
          <li className="nav-item">
            <Link
              to="/register"
              className={cx({ "nav-link": true, active: location.pathname === "/register" })}
            >
              Register
              <i className="fa fa-edit ml-2" />
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/login"
              className={cx({ "nav-link": true, active: location.pathname === "/login" })}
            >
              Login
              <i className="fa fa-sign-in ml-2" />
            </Link>
          </li>
        </>
      );
  }

  return (
    <>
      <li className="nav-item mr-3">
        <Form inline>
          <Input
            onChange={e => {
              switch (e.target.value) {
                case "production":
                  dispatch({
                    type: "DISABLE TEST KEY",
                  });
                  setKeyMode("production");
                  setAuthKey(btoa(`${user.data?.api_keys.live}:`));
                  break;
                case "testing":
                  dispatch({
                    type: "ENABLE TEST KEY",
                  });
                  setKeyMode("testing");
                  setAuthKey(btoa(`${user.data?.api_keys.test}:`));
                  break;
              }
              setTimeout(() => {
                window.location.replace("#/");
                window.location.reload();
              }, 0);
            }}
            type="select"
            value={useTestKey ? "testing" : "production"}
          >
            <option value="production">Production</option>
            <option value="testing">Testing</option>
          </Input>
        </Form>
      </li>
      <li className="nav-item">
        <Link
          to="/logout"
          className={cx({ "nav-link": true, active: location.pathname === "/logout" })}
        >
          Logout
          <i className="fa fa-sign-out ml-2" />
        </Link>
      </li>
    </>
  );
}

function SubActions(props: Props) {
  // hooks
  const {
    dispatch,
    state: { hideSidebar },
  } = useContext(UserContext)!;

  return (
    <button
      type="button"
      className="btn btn-outline-light"
      onClick={() => dispatch(ToggleRootSidebar())}
    >
      <span
        aria-hidden="true"
        className={hideSidebar ? "icon-arrow-right" : "icon-arrow-left"}
        style={{ fontSize: "12px" }}
      />
    </button>
  );
}

export function Navbar(props: Props) {
  const {
    state: { userAuth, useTestKey },
  } = useContext(UserContext)!;
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      {SubActions(props)}
      <Link to="/" className="navbar-brand ml-2">
        {props.appTitle}
      </Link>
      {!!userAuth && !useTestKey && (
        <Alert color="warning" className="mb-0 ml-4">
          <strong>The workbench is showing jobs used to train the predictive models.</strong>
        </Alert>
      )}
      <div className="collapse navbar-collapse">
        <ul className="navbar-nav ml-auto">{UserActions(props)}</ul>
      </div>
    </nav>
  );
}
